}

/* opacity */
.opa0 {
  opacity: 0;
  filer: alpha(opacity=0);
}

/* zoom */
.z {
  *zoom: 1;
}

/* ------------------- multiply CSS ------------------ */
/*  */
.auto {
  margin-left: auto;
  margin-right: auto;
}

/* */
.fix {
  *zoom: 1;
}
.fix:after {
  display: table;
  content: '';
  clear: both;
}

/* display:table-cell */
.cell {
  display: table-cell;
  *display: inline-block;
  width: 2000px;
  *width: auto;
}

/* cell */
.cell2 {
  overflow: hidden;
  _display: inline-block;
}

/*  */
.ell {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

/* css3 */
.trans {
  transition: all .15s;
}

/*  */
.dib_vm {
  display: inline-block;
  width: 0;
  height: 100%;
  vertical-align: middle;
}

/*  */
.bd_none {
  border: 0;
  outline: none;
}

/*  */
.abs_out {
  position: absolute;
  left: -999em;
  top: -999em;
}
.abs_clip {
  position: absolute;
  clip: rect(0 0 0 0);
}

/*  */
.disabled {
  outline: 0 none;
  cursor: default !important;
  opacity: .4;
  filer: alpha(opacity=40);
  -ms-pointer-events: none;
  pointer-events: none;
}

/*inline-blockfloat*/
.inline_box {
  font-size: 1em;
  letter-spacing: -.25em;
  font-family: Arial;
}
.inline_two, .inline_three, .inline_four, .inline_five, .inline_six, .inline_any {
  display: inline-block;
  *display: inline;
  letter-spacing: 0;
  vertical-align: top;
  *zoom: 1;
}
.float_two, .float_three, .float_four, .float_five, .float_six {
  float: left;
}
.inline_two, .float_two {
  width: 50%;
  *width: 49.9%;
}
.inline_three, .float_three {
  width: 33.33333%;
  *width: 33.3%;
}
.inline_four, .float_four {
  width: 25%;
  *width: 24.9%;
}
.inline_five, .float_five {
  width: 20%;
  *width: 19.9%;
}
.inline_six, .float_six {
  width: 16.66666%;
  *width: 16.6%;
}
.inline_fix {
  display: inline-block;
  width: 100%;
  height: 0;
  overflow: hidden;
}

