  font-size: 22px;
}
h4 {
  font-size: 18px;
}
h5 {
  font-size: 14px;
}
h6 {
  font-size: 12px;
}

/**
* abbr Firefox 
* 
*/
abbr,
acronym {
  border-bottom: 1px dotted;
  /* 1 */
  cursor: help;
  /* 2 */
}

/**
* Firefox3+Safari4/5  Chrome 
*/
b,
strong {
  font-weight: bold;
}

/**
*  Safari5  Chrome 
*/
dfn {
  font-style: italic;
}

/**
*  Firefox 
*/
hr {
  box-sizing: content-box;
  height: 0;
}

/**
* 
*  IE6-11 
*/
