* Mac  MonacoWindows  Consolas
* XP Courier New
* Windows 7 Consolas
* MacMonacoOsaka-Mono
*/
code,
kbd,
pre,
samp {
  font-family: monaco, menlo, consolas, 'courier new', courier, monospace;
}

/**
*  pre 
* 1. IE 6-7  pre-wrap
* 2. pre 
*/
pre {
  white-space: pre;
  white-space: pre-wrap;
  /* 1 */
  word-wrap: break-word;
  overflow: auto;
}

/**
* 
* IE 6-7  quotes 
*  quotes 
*/
q {
  quotes: none;
}

/**
* Safari 4 <q>
*/
q:before,
q:after {
  content: '';
  content: none;
}

/**
* <small> 12px 
*/
small {
  font-size: 85.7%;
  /* 12/14=0.8571428571 */
}

/**
* <sub><sup>
* http://jsbin.com/usoyal/1/edit
*/
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
