  /* 2 */
  *margin-left: -7px;
  /* 3 */
}

/**
* 1. 
* 2. 
* 3.  Firefox 3+ Safari5  Chrome 
* 4. 
*/
button,
input,
select,
textarea {
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 2 */
  margin: 0;
  /* 3 */
  vertical-align: baseline;
  /* 4 */
  *vertical-align: middle;
  /* 4 */
}

/**
*  IE7 
*/
