  text-transform: none;
}

/**
* 1.  Android 4.0.*  WebKit bug bug
 audio video
* 2.  iOS input
* 3. input
*/
button,
html input[type="button"], input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  /* 2 */
  cursor: pointer;
  /* 3 */
}

/**
* 
*/
button[disabled],
input[disabled] {
  cursor: default;
  opacity: .6;
}

/**
* 1.  IE 8/9 box-sizing content-box
* 2.  IE 8/9 
* 3.  IE7 (IE6 )
*/
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
  *height: 13px;
  /* 3 */
  *width: 13px;
  /* 3 */
}

/**
* 1.  Safari 5  Chrome appearancesearchfield
* 2.  Safari 5  Chrome box-sizing border-box
*/
input[type="search"] {
  -webkit-appearance: textfield;
  /* 1 */
  box-sizing: content-box;
}

/**
* 1. OS X  Safari5  Chrome 
* 2.
 input[type="search"]::-webkit-search-cancel-button
*/
