*/
[draggable] {
  cursor: move;
}

/**
*  HTML5 dialog 
* Chrome 31  chrome://flags/#enable-experimental-web-platform-features
* Chrome 28 Firefox  height:fit-content;
 https://src.chromium.org/viewvc/blink?revision=148314&view=revision
* ::backdrop 
* @demo: http://jsbin.com/iPACab/1
*/
dialog {
  border: 1px solid;
  padding: 0;
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
dialog::-webkit-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
}
dialog::backdrop {
  position: fixed;
  top: 0;
