  color: #FFF;
  box-shadow: 0 0 3px #66BB6A;
  background: #66BB6A;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.z-header .header-logo {
    -webkit-box-flex: 2;
        -ms-flex: 2;
            flex: 2;
}
.z-header .header-nav {
    line-height: 60px;
    -webkit-box-flex: 8;
        -ms-flex: 8;
            flex: 8;
}
.z-header .header-right {
    -webkit-box-flex: 2;
        -ms-flex: 2;
            flex: 2;
    line-height: 60px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
